import { Button as MuiButton, Grid } from '@mui/material'
import { makeStyles } from '@material-ui/core'
import { viewMore, results } from '../../../Constants/constants'
import { SearchBar } from '../../Atoms/SearchBar/SearchBar'
import Icon from '../../Atoms/Icon/Icon'
import TypographyComponent from '../../Atoms/Typography/TypographyComponent'
import { FooterCardItem } from '../../Molecules/FooterCardItem/FooterCardItem'
import { useState } from 'react'

export type OptionType = {
  src: any
  text: string
}
export type footerProps = {
  footerItems: Array<OptionType>
}

export const FooterList = (props: footerProps) => {

    const [showScroll, setShowScroll] = useState(false)
    const checkScrollTop = () => {    
       if (!showScroll && window.pageYOffset > 400){
          setShowScroll(true)    
       } else if (showScroll && window.pageYOffset <= 400){
          setShowScroll(false)    
       }  
    };
    window.addEventListener('scroll', checkScrollTop)

  const { footerItems } = props
  const useStyles = makeStyles((theme) => ({
    Item: {},
    results: {},
    viewMore: {},
  }))

  const list=(<Grid container direction="row" spacing={10} style={{}}>
  {footerItems.map((item: OptionType, index: number) => { 
return(
<Grid item style={{}}>
<FooterCardItem iconUrl={item.src} text={item.text}></FooterCardItem>
</Grid>
)})}
</Grid>)

  const classes = useStyles()
  return (
    <><Grid container direction="row" spacing={10} style={{}}>
          {footerItems.map((item: OptionType, index: number) => {
              return (
                  <Grid item style={{}}>
                      <FooterCardItem iconUrl={item.src} text={item.text}></FooterCardItem>
                  </Grid>
              )
          })}
      </Grid><a className="next" onClick={this.scroll.bind(null, 1)}>&#10095;</a></>
  
  )}
